import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Style-module
import styles from '../projects.module.scss';
// Types
interface ILogoProjectProps {
  logo?: JSX.Element;
  petProject: boolean;
}

const LogoProject: React.FC<ILogoProjectProps> = ({ logo, petProject }) => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <div className={styles.content__logo}>
      {logo}
      <span className={styles.content__view} role="note">
        {lang.bool ? 'быстрый просмотр' : 'quick view'}
      </span>
      {petProject && (
        <span className={styles.content__pet} role="note">
          {lang.bool ? 'пет-проект' : 'pet-project'}
        </span>
      )}
    </div>
  );
};

export default LogoProject;
