import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Utils
import { toggleClassEl } from '../../../../utils/ts/toggleClassEl';
// Components
import Icon from '../../../Common/Icon';
// Styles-module
import styles from '../projects.module.scss';
// Types
interface IBtnDescProps {
  name: string;
}

const BtnDesc: React.FC<IBtnDescProps> = ({ name }) => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <button
      id={`content-btn-${name}`}
      className={styles.content__btn}
      onClick={() =>
        toggleClassEl(
          ...Array.from(['box', 'btn'], (el) => {
            return {
              selector: `#content-${el}-${name}`,
              cssClass: styles[`content__${el}-active`],
            };
          })
        )
      }
      aria-label={lang.bool ? 'Открыть описание' : 'Open description'}
      children={<Icon active="arrow" size={{ width: 17, height: 30 }} />}
    />
  );
};

export default BtnDesc;
