import React, { useState, useContext, useEffect } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Utils
import { convertInString } from '../../../utils/js/сonvert';
import { toggleClassEl } from '../../../utils/ts/toggleClassEl';
// Swiper-Slider
import { A11y, Autoplay, EffectFade } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
// Styles Swiper-Slider
import 'swiper/css';
import 'swiper/css/a11y';
import 'swiper/css/autoplay';
import 'swiper/css/effect-fade';
// Components
import Blockquote from '../../Common/Blockquote';
import UsTitle from '../../Common/UsTitle';
import Picture from '../../Common/Picture';
import BtnDesc from './BtnDesc';
import LogoProject from './LogoProject';
import StackList from './StackList';
import ViewLink from './ViewLink';
// Styles-module
import styles from './projects.module.scss';
import style from './styles';
// Assets
import { image } from '../../../assets';

// Remove active css-classes
export const removeActiveClass = (value: string) =>
  toggleClassEl(
    ...Array.from(['box', 'btn'], (el) => {
      return {
        selector: `#content-${el}-${value}`,
        cssClass: styles[`content__${el}-active`],
        act: 'remove',
      };
    })
  );

const Projects: React.FC = () => {
  // React Context
  const { screenWidth, lang, data } = useContext(sectionContext);
  const { path, projects }: DataProjects = data;
  const screenSize = screenWidth <= 1141;
  // React State
  const [animateActive, setAnimateActive] = useState<boolean>(false);
  const [itemHidden, setItemHidden] = useState<boolean>(screenSize);
  useEffect(() => {
    setTimeout(() => setAnimateActive(true), 200);
  }, []);
  useEffect(() => {
    setItemHidden(screenSize);
  }, [screenSize]);

  return (
    <>
      <Blockquote
        blockquote={
          lang.bool
            ? 'Успех — это способность идти от неудачи к неудаче, не теряя энтузиазма'
            : 'Success is the ability to go from failure to failure without losing your enthusiasm'
        }
        author={lang.bool ? 'Уинстон Черчилль' : 'Winston Churchill'}
      />
      <ul className={styles.list}>
        {projects
          .map((o) => ({ ...o, source: image.projects[o.name] }))
          .slice(itemHidden ? 0 : undefined, itemHidden ? 3 : undefined)
          .map(({ name, pet, online, stack, source }, i) => (
            <li
              key={`project-${i + 1}`}
              className={convertInString(
                styles.item,
                animateActive && styles.item__active
              )}
              style={{
                transitionDelay: `${(i / 3).toFixed(1)}s`,
              }}
            >
              <Swiper
                modules={[A11y, Autoplay, EffectFade]}
                loop={true}
                allowTouchMove={false}
                effect={'fade'}
                onSwiper={(swiper) => {
                  // @ts-ignore
                  swiper.params.speed = 500;
                  // @ts-ignore
                  swiper.params.autoplay.delay = 1000;
                  // @ts-ignore
                  swiper.params.autoplay.disableOnInteraction = false;

                  swiper.el.addEventListener('mouseenter', () =>
                    swiper.autoplay?.start()
                  );
                  swiper.el.addEventListener('mouseleave', () => {
                    if (swiper.autoplay) {
                      swiper.autoplay.stop();
                      return swiper.slideTo(1, 1000);
                    }
                  });
                }}
              >
                {source?.images.map((path, i) => (
                  <SwiperSlide key={`${name}-${i + 1}`}>
                    <Picture
                      src={path}
                      alt={`${name}-${i + 1}`}
                      cssClasses={{ img: styles.img }}
                    />
                  </SwiperSlide>
                ))}
                <div
                  id={`content-box-${name}`}
                  className={styles.content__box}
                  onMouseLeave={() => {
                    removeActiveClass(name);
                    document.getElementById(`content-btn-${name}`)?.blur();
                  }}
                >
                  <BtnDesc name={name} />
                  <LogoProject logo={source?.logo} petProject={pet} />
                  <div className={styles.content}>
                    <div className={style.stackContent}>
                      <UsTitle
                        level={2}
                        content={
                          lang.bool ? 'Стек технологий' : 'Technology stack'
                        }
                        cssClass={style.stackTitle}
                      />
                      <StackList stack={stack} />
                    </div>
                    <div className={styles['content__link-box']}>
                      {online && (
                        <ViewLink
                          value="online"
                          path={path.online}
                          title={name}
                        />
                      )}
                      <ViewLink
                        value="source"
                        path={path.source}
                        title={name}
                      />
                    </div>
                  </div>
                </div>
              </Swiper>
            </li>
          ))}
      </ul>
      {itemHidden && (
        <button
          className={style.btnVisible}
          onClick={() => setItemHidden(false)}
          children={`${lang.bool ? 'Показать ещё' : 'Show more'}`}
        />
      )}
    </>
  );
};

export default React.memo(Projects);
