import React from 'react';
// Styles-module
import styles from '../projects.module.scss';
import style from '../styles';
// Types
interface IStackListProps {
  stack: string[];
}

const StackList: React.FC<IStackListProps> = ({ stack }) => {
  return (
    <ul className={styles['content__stack-list']}>
      {stack.map((value, i) => (
        <li key={`stack-${i + 1}`} className={styles['content__stack-item']}>
          <strong className={style.stack} translate="no" lang="en">
            - {value}
          </strong>
        </li>
      ))}
    </ul>
  );
};

export default StackList;
