import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../../context/sectionContext';
// Utils
import { removeActiveClass } from '..';
import { capitalizedString } from '../../../../utils/js/сonvert';
// Styles-module
import styles from '../projects.module.scss';
// Types
interface IViewLinkProps {
  value: string;
  path: string;
  title: string;
}

const ViewLink: React.FC<IViewLinkProps> = ({ value, path, title }) => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <a
      id={`${title}-${value}`}
      className={styles.content__link}
      href={`${path}${title}`}
      target="blanck"
      onClick={() => removeActiveClass(title)}
      onBlur={(e) =>
        e.currentTarget.id === `${title}-source` && removeActiveClass(title)
      }
    >
      {capitalizedString(
        value === 'online'
          ? lang.bool
            ? 'онлайн'
            : value
          : lang.bool
          ? 'исходный код'
          : value
      )}
    </a>
  );
};

export default ViewLink;
